import React,{useEffect,useState} from 'react';

const Grid4 = (props)=>{
    const[prop,setProp] = useState({backgrounds : [],images : [],title : []});
    useEffect(()=>{
        setProp(props.state);
    },[prop])
    return(
    <div className="column is-half" style={{visibility:props.visible}}>
        <div className="columns is-multiline _query">
            <div className="column is-half">
                <p style={{background:prop.backgrounds[1],height:"calc(176px)"}} class="middle">
                    <img src={prop.images[0]} alt={prop.title[0]}/>
                    <p>{prop.title[0]}</p>
                </p>
            </div>
            <div className="column is-half">
                <p style={{background:prop.backgrounds[2],height:"calc(176px)"}} class="middle">
                    <img src={prop.images[1]} alt={prop.title[1]}/>
                    <p>{prop.title[1]}</p>
                </p>
            </div>
            <div className="column is-half">
                <p style={{background:prop.backgrounds[3],height:"calc(176px)"}} class="middle">
                <img src={prop.images[2]} alt={prop.title[2]}/>
                    <p>{prop.title[2]}</p>
                </p>
            </div>
            <div className="column is-half" style={{}}>
                <div style={{background:prop.backgrounds[4],height:"calc(176px)"}} class="middle">
                    <img src={prop.images[3]} alt={prop.title[3]}/>
                    <p>{prop.title[3]}</p>
                </div>
            </div>
        </div>
    </div>
    );
}
export default Grid4;