import React,{useEffect,useState} from 'react';

const Grid2 = (props)=>{
    const[prop,setProp] = useState({backgrounds : [],images : [],title : []});
    const img = "https://bulma.io/images/placeholders/1280x960.png";
    useEffect(()=>{
        try{
            if(props.images!=null)if(props.images.length>1)throw new Error("Array Index of Exception");
            if(props.backgrounds!=null)if(props.backgrounds.length>2)throw new Error("Array Index of Exception");
            if(props.component!=null)if(props.component.length>1)throw new Error("Array Index of Exception");
            setProp({
                backgrounds : props.backgrounds||["red","red"],
                images : props.images||[img],
                component : props.component||"recive un componente"
            })
        }catch(e){
            console.error(e)
        }

    },[])
    return(
        <>
        <div className="columns is-multiline" style={{textAlign:"center",height:"auto",background:prop.backgrounds[0],marginLeft:"-1.5rem",marginRight:"-1.5rem"}}>
            <div class="column is-full">{props.children}</div>
            <div className="column">
                {prop.component}
            </div>
            <div className="column">
                <img style={{height:"calc(376px)"}} src={prop.images[0]} alt=""/>
            </div>
        </div>
        </>
    );
}
export default Grid2;