import React,{useEffect,useState} from 'react';
import Grid4 from './Grid4';
const Grid16 = (props)=>{
    const[prop,setProp] = useState({backgrounds : [],images : [],title : [], columnVisible : []});
    const img = "https://bulma.io/images/placeholders/1280x960.png";
    const colors = ["red","red","red","red"];
    useEffect(()=>{
        try{
            if(props.images!=null)if(props.images.length>4)throw new Error("Array Index of Exception");
            if(props.backgrounds!=null)if(props.backgrounds.length>5)throw new Error("Array Index of Exception");
            if(props.title!=null)if(props.title.length>4)throw new Error("Array Index of Exception");
            if(props.columnVisible!=null)if(props.columnVisible.length>5)throw new Error("Array Index of Exception");
            setProp({
                backgrounds : props.backgrounds||["red","red","red","red","red"],
                images : props.images||["","","",""],
                title : props.title||["title","title","title","title"],
                columnVisible : props.columnVisible||["visible","visible","visible","visible"]
            })
        }catch(e){
            console.error(e)
        }

    },[])
    return(
        <>
        <div className="columns is-multiline" style={{textAlign:"center",height:"auto",background:prop.backgrounds[0],marginLeft:"-1.5rem",marginRight:"-1.5rem"}}>
            <div class="column is-full">{props.children}</div>
            <Grid4 state={prop} visible={prop.columnVisible[0]}>
            
            </Grid4>
            <Grid4 state={prop} visible={prop.columnVisible[1]}>
                
            </Grid4>
            <Grid4 state={prop} visible={prop.columnVisible[2]}>
                
            </Grid4>
            <Grid4 state={prop} visible={prop.columnVisible[3]}>
                
            </Grid4>
     
        </div>
        </>
    );
}
export default Grid16;